.card {
    border: 1px solid #ccc;
    height: 340px;
    padding: 10px;
    margin-bottom: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    transform-style: preserve-3d;
    perspective: 1000px;
}

.card:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card p {
    margin-left: 10px;
    color: rgb(124, 115, 115);
}

.card h3 {
    margin: 5px 0;
    margin-bottom: 0;
    margin-left: 10px;
}

.card img {
    height: 240px;
    width: 100%;
}

#company_upper_box {
    height: min-content;
    margin-bottom: 50px;
    background-color: #F3F9F5;
    padding: 0 15%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

#company_upper_box>img {
    margin-top: -50px;
    height: 300px;
    width: auto;
}

#inside_upper_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

#inside_upper_box h2 {
    margin-bottom: 20px;
    font-size: 30px;
}

#inside_upper_box p {
    font-size: 18px;
    color: rgb(101, 94, 94);
}

#search div {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

#search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px;
    flex-direction: column;
}

#search input {
    height: 30px;
    margin: 10px 0;
    font-size: large;
    outline: none;
    width: 100%;
    max-width: 300px;
}

#search p {
    margin-top: 5px;
    font-size: large;
    color: rgb(86, 80, 80);
}

#search button {
    padding: 5px 20px;
    font-size: large;
    background-color: #14a36f;
    color: white;
    border: none;
    border-radius: 10px;
    margin-top: 10px;
    width: 100%;
    max-width: 150px;
}

#search button:hover {
    transform: scale(1.05);
}

#card_container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: min-content;
    padding: 20px 10%;
    grid-gap: 50px 80px;
    margin-bottom: 30px;
}

#head {
    font-size: 40px;
    margin: 0 10%;
    margin-bottom: 10px;
}

@media (max-width: 1080px) {
    #card_container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 768px) {
    #card_container {
        grid-template-columns: repeat(2, 1fr);
    }

    #company_upper_box {
        padding: 0 5%;
    }

    #company_upper_box>img {
        height: 200px;
        margin-right: 0;
    }

    #inside_upper_box h2 {
        font-size: 24px;
    }

    #inside_upper_box p {
        font-size: 16px;
    }

    #head {
        font-size: 32px;
        margin: 0 5%;
    }
}

@media (max-width: 480px) {
    #card_container {
        grid-template-columns: 1fr;
    }

    #search input {
        width: 100%;
    }

    #search button {
        max-width: 100%;
    }

    #inside_upper_box h2 {
        font-size: 20px;
    }

    #inside_upper_box p {
        font-size: 14px;
    }

    #head {
        font-size: 28px;
        margin: 0 5%;
    }
}

.see {
    position: relative;
    color: rgb(107, 96, 96);
}

.card-link {
    text-decoration: none;
}

.card h3,
.card p {
    text-decoration: none;
}
