#footer_title {
    font-size: 180px;
}

#footer_img {
    height: 200px;
    margin-left: 60px;
    margin-right: 50px;
}

#footer_div {
    display: flex;
    align-items: center;
    border-top: 2px solid rgba(128, 128, 128, 0.215);
    padding-top: 40px;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
}

#first_letter {
    color: #21b47e;
}

#footer_div > span {
    padding: 0px;
    margin: 0px;
    display: inline-block;
}

#lower_div {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    width: max-content;
    margin: auto;
}

#lower_div h3 {
    color: grey;
    line-height: 50px;
}

#lower_div div {
    margin: 0px 30px;
}

#outer_lower_second {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
    width: 100%;
}

#lower_div p {
    font-weight: 600;
    line-height: 23px;
}

.icon_is {
    transform: scale(1.3);
    margin: 0px 20px;
}

#icon_container {
    display: flex;
    justify-content: center;
    padding-top: 80px;
    padding-bottom: 20px;
}

@media (max-width: 768px) {
    #footer_div {
        flex-direction: column;
        text-align: center;
    }

    #footer_img {
        margin: 0 auto 20px;
    }

    #footer_title {
        font-size: 100px;
    }

    #outer_lower_second {
        flex-direction: column;
    }

    #lower_div {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    #lower_div div {
        margin: 20px 0;
    }

    #icon_container {
        padding-top: 40px;
    }
}

@media (max-width: 768px) {
    #footer_title {
        font-size: 60px;
    }

    .icon_is {
        transform: scale(1);
        margin: 0px 10px;
    }
}
