/* Existing Styles (unchanged) */
#body_header {
    background-color: #21b47e;
    margin: 30px 100px;
    border-radius: 30px;
    padding: 20px;
    position: relative;
    color: white;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 10px;
}

#body_header_1 {
    height: 500px;
    position: relative;
    bottom: 5px;
    left: 50px;
}

#user_detail {
    margin-left: 30px;
}

#second_ip {
    position: relative;
}

#head_sentence_1 {
    font-size: 100px;
    color: white;
    margin: 20px 10px 0px 60px;
}

#head_sentence_2 {
    font-size: 100px;
    color: white;
    margin: 0px 10px 20px 60px;
}

#head_sentence {
    font-size: 30px;
    color: rgba(255, 255, 255, 0.749);
    margin: 0px 10px 20px 60px;
}

.btn {
    border: 0px;
    padding: 15px 40px;
    border-radius: 20px;
    color: white;
    background-color: black;
    font-weight: 700;
    font-size: large;
    margin: 15px 10px 20px 60px;
}

.btn:hover {
    transform: scale(1.03);
}

#first_inside > img {
    height: 450px;
    margin: 40px 0px 20px 60px;
}

#second_head {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
#yoo{
    width: max-content;
}
#second_inside {
    padding-left: 20px;
    padding-top: 70px;
}

#second_inside > h1 {
    font-size: 80px;
}

#second_inside > h3 {
    margin-top: 20px;
    font-size: 25px;
    color: rgba(128, 128, 128, 0.968);
}

#second_inside > button {
    margin-left: -10px;
    margin-top: 30px;
}

#user_name {
    text-decoration: underline solid rgb(40, 40, 180);
}

@media (max-width: 1200px) {
    #body_header {
        margin: 30px 50px;
        grid-template-columns: 1fr;
        text-align: center;
    }

    #body_header_1 {
        height: 400px;
        left: 0;
        margin-top: 20px;
    }

    #head_sentence_1,
    #head_sentence_2 {
        font-size: 80px;
        margin: 10px;
    }

    #head_sentence {
        font-size: 24px;
        margin: 10px;
    }

    .btn {
        padding: 15px 30px;
        font-size: medium;
        margin: 15px 10px 20px;
    }

    #first_inside > img {
        height: 350px;
        margin: 40px 0 20px;
    }

    #second_head {
        grid-template-columns: 1fr;
        text-align: center;
    }

    #second_inside {
        padding-left: 10px;
        padding-top: 40px;
    }

    #second_inside > h1 {
        font-size: 60px;
    }

    #second_inside > h3 {
        font-size: 20px;
    }

    #second_inside > button {
        margin-left: 0;
        padding: 12px 24px;
        font-size: medium;
        margin-top: 20px;
    }
}

@media (max-width: 768px) {
    #body_header {
        margin: 30px 10px 30px 40px;
        /* width: min-content; */
        grid-template-columns: 1fr;
    }

    #body_header_1 {
        height: 300px;
    }

    #head_sentence_1,
    #head_sentence_2 {
        font-size: 60px;
    }

    #head_sentence {
        font-size: 20px;
    }

    .btn {
        padding: 12px 24px;
        font-size: small;
    }

    #first_inside > img {
        height: 250px;
    }

    #second_inside > h1 {
        font-size: 40px;
    }

    #second_inside > h3 {
        font-size: 18px;
    }
}
