#register_head{
    font-size: 80px;
    margin: 20px 60px;
    margin-top: 30px;
    margin-bottom: 10px;
    
}
#register_head_1{
    color: #21b47e;
}
#register_main{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 10px 60px;
}
#img_11{
    height: 600px;
    margin-left: 80px;
    margin-top: 0px;
   
}
.MuiFormControl-root{
    height: 60px;
    width: 550px;
   margin: 20px;
}
#detail{
    color: rgba(0, 0, 0, 0.715);
    margin-top: 30px;
    margin-bottom: 10px;
     font-size: 30px;
}
#main_1{
    padding-left: 100px;
    position: relative;
}
#standard-basic{
    font-size: 20px;
}
#outlined-basic{
    font-size: 20px;
    height: 80px;
}

#outlined{
    margin-top: 30px;
}
.register_input{
    margin-top: 20px;
}
#up{
color: white;

}

#file_con{
    /* border: 2px solid rgba(0, 0, 0, 0.854); */
    padding: 10px;
}
#cus{
    border: 1px solid black;
    position: relative;
    top: 60px;
    padding: 0px;
    width: 85px;
    background-color:#21b47e;
    z-index: 100;

}
#file-upload{
        display: inline-block;
        padding-left: 15px;
        padding-top: 8px;
}
::-webkit-file-upload-button{
    display: none;
  }
.custom-file-upload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
  }
  #yoo{
    background-color: rgb(36, 166, 217);
    color: white;
    width: max-content;
    font-weight: 600;
    margin-left: 20px;
    border-radius: 20px;
  }
  #file_con{
    display: flex;
    margin-top: 30px;
  }
  #file_h{
    font-weight: 500;
    color: rgba(0, 0, 0, 0.616);
    position: relative;
    top: 5px;
  }
  #btn_re{
    margin: 40px 0px;
    width: 300px;
    position: relative;
    left: 90px;
  }
  #Finish{
    position: absolute;
    height: 300px;
    width: 300px;
    top: 45%;
    right: 45%;
  }
  @media (max-width:1286px) {
    #register_head{
      margin-left: 20px;
    }
    #main_1{
      padding: 0px;
    }
    
  }
  @media (max-width:1271px) {
    #img_11{
       margin-left: 0px;
    }
  }

  @media (max-width:1190px) {
    #register_main{
      margin:20px 20px;
    }
    #detail{
      margin: 0px;
    }
    #register_head{
      margin: 0px 10px;
    }
  }
  @media (max-width:1150px) {
    #img_11{
      height: 500px;
    }
  }
  @media (max-width:1050px) {
    #img_11{
      height: 400px;
    }
    #register_main {
     grid-template-columns: 1fr;
  }
  #main_1 form {
   padding-left: 30px;
}
  }
  
  @media (max-width:1150px) {
    #main_1 form {
      padding-left: 0px;
   }
  }