#main_login {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    padding: 20px;
}

#img_222 {
    width: 100%;
    height: auto;
    margin: 0 auto;
}

#login_second {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr ;
    align-items: center;
    justify-items: center;
    text-align: center;
}

#login_second h2 {
    font-size: 3vw; /* Responsive font size */
    color: rgba(0, 0, 0, 0.788);
    margin: 0;
}

#under_second button {
    background-color: #1fc286;
    padding: 15px 20px;
   margin-top: 30px;
    max-width: 500px;
    color: white;
    font-weight: 700;
    font-size: 30px;
    border: none;
    border-radius: 25px;
   
}

#under_second button:hover {
    transform: scale(1.1);
}

@media (max-width: 768px) {
    #main_login {
        grid-template-columns: 1fr;
        gap: 10px;
    }

    #login_second h2 {
        font-size: 5vw;
    }

    #under_second button {
        
        /* font-size: larger; */
    }
}
