/* Existing CSS remains unchanged */

#hjkl{
  display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
}
#pie_chart h2{
  margin-top: 30px;
}
.box_shadow{
  box-shadow: 0px 0px 10px rgba(41, 36, 36, 0.523);
  border-radius: 20px;
  margin:40px 150px;
}
.all_review{
  box-shadow: 0px 0px 10px rgba(41, 36, 36, 0.523);
  border-radius: 20px;
  padding: 20px 30px;
  margin-top: 30px;
}
#overall_review{
  display: flex;
  align-items: center;
}
.over_v{
  display: flex;
  align-items: center;
}
#add_video{
  height: 300px;
}
.over_v h2{
  width: max-content;
  margin-right: 15px;
}
#container_review h1{
  margin-bottom: 30px;
  color: rgba(0, 0, 0, 0.511);
}
#company_head{
  color: rgba(0, 0, 0, 0.511);
}
.over_v{
  margin-top: 20px;
  margin-bottom: 5px;
}
#last_div{
  padding: 0px 80px;
}
#last_div h1{
 margin-bottom: 30px;
 margin-top: 60px;
}
#container_review path{
     color: rgb(35, 177, 125);
     
}
.rate_icon{
transform: scale(1.3);
margin: 10px 10px 0px 20px;
}
#overall_review svg{
  transform: scale(1.5);
}
.all_review path{
  color: rgb(35, 177, 125); 
  transform: scale(1.1);
  margin: 20px 30px;
}
/* New media queries for screens below 670px */
@media (max-width: 669px) {
    #Company_component,
    #container_review,
    #review_div {
      flex-direction: column;
      align-items: center;
      padding: 20px 30px;
    }
  
    #company_details,
    #com_img,
    #company_review,
    .all_review {
      width: 100%;
      max-width: none;
      margin: 10px 0;
    }
  
    #com_img img {
      max-width: 100%;
      height: auto;
    }
  
    .over_v {
      flex-direction: column;
      align-items: flex-start;
    }
  
    #overall_review {
      margin-bottom: 10px;
    }
  
    h1 {
      font-size: 1.6em;
    }
  
    h2 {
      font-size: 1.3em;
    }
  
    h3 {
      font-size: 1.1em;
    }
}
  
/* Additional adjustments for very small screens */
@media (max-width: 480px) {
    #Company_component,
    #container_review,
    #footer_review {
      padding: 10px;
    }
  
    .all_review {
      padding: 15px;
    }
  
    h1 {
      font-size: 1.4em;
    }
  
    h2 {
      font-size: 1.2em;
    }
  
    h3 {
      font-size: 1em;
    }
}

/* New media queries for screens above 670px */
@media (min-width: 670px) {
    #Company_component {
        display: flex;
        justify-content: space-between;
        padding: 20px;
    }

    #container_review {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        padding: 20px;
    }

    #review_div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding: 20px;
    }

    #company_details,
    #com_img,
    #company_review,
    .all_review {
        width: 45%;
        max-width: 600px;
        margin: 10px 0;
    }

    #com_img img {
        max-width: 100%;
        height: auto;
    }

    .over_v {
        flex-direction: row;
        align-items: center;
    }

  

    h1 {
        font-size: 2em;
    }

    h2 {
        font-size: 1.5em;
    }

    h3 {
        font-size: 1.2em;
    }
}

/* Additional adjustments for very large screens */
@media (min-width: 1024px) {
    #Company_component {
        padding: 40px;
    }

    #container_review {
        padding: 40px;
    }

    #review_div {
        padding: 40px;
    }

    #company_details,
    #com_img,
    #company_review,
    .all_review {
        width: 40%;
        max-width: 800px;
        margin: 20px 0;
    }
    .all_review{
      margin-top: 30px;
      width: max-content;
    }
    h1 {
        font-size: 2.5em;
    }

    h2 {
        font-size: 2em;
    }

    h3 {
        
    }
}




#container_review {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  
}

#company_review {
  width: 100%;
  max-width: 800px;
  margin-bottom: 20px;
}

#review_section {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  margin-bottom: 20px;
}


#pie_chart{
  width: min-content;
}
#pie_chart h2{
  font-size: 25px;
  width: max-content;
}


@media (max-width: 669px) {
  #container_review {
    flex-direction: column;
  }

  #review_section {
    flex-direction: column;
    align-items: center;
  }

  #add_video, #pie_chart {
   
    margin-bottom: 10px;
  }
} 
@media (max-width:1200px) {
  #pie_chart h2{
    font-size: 20px;
  }
}
@media (max-width:1080px) {
  #review_section{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .box_shadow{
    margin: 40px;
  }
}
@media (max-width:500px) {
  #add_video{
    height: 250px;
  }
  #hjkl{
    justify-items: start;
    margin-bottom: 20px;
  }
  #pie_chart h2 {
    margin-left: -20px;
    font-size: 15px;
  }
  #add_your h2{
    margin-right: 5px;
  }
  #pie_chart canvas{
    transform: scale(0.8);
margin-left: -80px;
  }

}