body, html {
    margin: 0;
    height: 100%;
  }
  
  .unique-overlay-12345 {
    z-index: 100000000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* semi-transparent dark background */
    backdrop-filter: blur(10px); /* applies blur to everything behind this element */
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
  }
  
  .unique-overlay-12345.unique-visible-12345 {
    opacity: 1;
    pointer-events: all;
  }
  
  .unique-alert-box-12345 {
    position: relative;
    background: rgba(255, 255, 255, 0.8); /* semi-transparent white background */
    backdrop-filter: blur(5px); /* applies blur to the background */
    border: 1px solid rgba(255, 255, 255, 0.2); /* light border for glass effect */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); /* soft shadow */
    text-align: center;
    z-index: 10;
  }
  
  .unique-close-btn-12345 {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  .unique-message-12345 {
    margin: 20px 0;
  }
  
  .unique-ok-btn-12345 {
    padding: 10px 20px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .unique-ok-btn-12345:hover {
    background-color: #0056b3;
  }
  