/* Base styles */
nav {
  background-color: #fff;
  overflow: hidden;
  padding: 10px;
  position: sticky;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#user_detail {
  display: flex;
}

#user_icon {
  border-radius: 50%;
  height: 40px;
}

* {
  color: black;
  margin: 0;
  padding: 0;
}

#logo_div {
  padding-left: 20px;
  display: flex;
  align-items: center;
}

#option_nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#option_nav > button {
  margin-right: 70px;
}

nav img {
  height: 50px;
  margin-right: 10px;
}

nav a {
  color: black;
  text-align: center;
  padding: 18px 16px;
  text-decoration: none;
  font-size: 17px;
  font-weight: 700;
}

nav a:hover {
  background-color: #02A65C;
  color: white;
}

#user_name {
  font-size: large;
  align-self: center;
  margin: 0 0 0 10px;
  color: rgba(0, 0, 255, 0.781);
  text-decoration: solid rgb(22, 98, 219);
}

.login-btn {
  background-color: rgb(41, 37, 37);
  color: white;
  font-weight: 600;
  margin-top: 7px;
  margin-bottom: 7px;
  margin-left: 15px;
  border: none;
  padding: 5px 15px;
  border-radius: 10px;
  cursor: pointer;
  float: right;
}

.login-btn:hover {
  transform: scale(1.05);
}

/* Responsive styles */
.nav-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  .nav-toggle {
    display: block;
  }

  #option_nav {
    display: none;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 10px;
  }

  #option_nav.open {
    display: flex;
    padding-top: 20px;
  }

  #option_nav a,
  #option_nav .login-btn {
    width: 100%;
    text-align: center;
    padding: 10px;
    margin: 5px 0;
  }

  #option_nav > button {
    margin-right: 0;
  }

  #user_name {
    margin: 10px 0;
  }

  #logo_div {
    display: flex;
  }

  .hidden {
    display: none;
  }
}
#back_icon{
  position: absolute;
  top: 20px;
  right: 20px;
}