#feedback-form-container {
    padding: 20px;
    max-width: 400px;
    margin: auto;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  #feedback-form-header {
    margin-bottom: 20px;
  }
  
  #feedback-form-button-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  #feedback-form-positive-button,
  #feedback-form-negative-button {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  #feedback-form-feedback-text {
    margin: 10px 0;
    font-style: italic;
    color: #555;
  }
  
  #feedback-form-submit-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
#feedback-form-feedback-text{
    margin-left: 130px;
}  